import React, {useEffect} from 'react';
import { connect } from 'react-redux';
import styles from './ButtonNavigationPrivacyPolicy.module.css';
import { PRIVACY_POLICY_STATE_PROPERTY } from '../../../../reducer/constants';
import { PRIVACY_POLICY_ACTION, ACTION_DISPATCHER } from '../../../../actions/constants';

import { allActions } from '../../../../actions';
const styleSelected  = {}
styleSelected['selected'] = {}
styleSelected['selected']['button'] = {backgroundColor: '#000000'}
styleSelected['selected']['text'] = {color: '#FFFFFF'}
styleSelected['notSelected'] = {}
styleSelected['notSelected']['button'] = {}
styleSelected['notSelected']['text'] = {}
export function ButtonNavigationPrivacyPolicy(props) {
    const [selection, setSelection] = React.useState('notSelected');
    const nameOfComp = props.name
    useEffect(() => {
        
        if (props.privacyPolicy === nameOfComp)
        {
            setSelection('selected')
        }      
        else {
            setSelection('notSelected')
        }
      }, [props.privacyPolicy]);
    const handleClick = (event) => {
        event.preventDefault()
        props.setPrivacyPolicy(nameOfComp)

    }
    return (

                    <button className={styles.secondaryContainer} style={styleSelected[selection]['button']} onClick={handleClick}>
                            <span className={styles.buttonText} style={styleSelected[selection]['text']}>{props.textButton} </span>
                    </button>
                
    )
}


const mapStateToProps = (state) => {
    return {
    
      privacyPolicy: state[PRIVACY_POLICY_STATE_PROPERTY],
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setPrivacyPolicy: (payload) => dispatch(allActions[PRIVACY_POLICY_ACTION][ACTION_DISPATCHER](payload)),
    }
  }
 export default connect(mapStateToProps, mapDispatchToProps)(ButtonNavigationPrivacyPolicy);