const allActions = {}

const createActionDispatcher = (nameOfAction) => {
    return (payload = null) => 
    {
        const action = {}
        action.type = nameOfAction
        action.payload = payload
        return action;
    }
}
allActions[CLICK_BUTTON_ACTION] = {}

allActions[CLICK_BUTTON_ACTION][ACTION_DISPATCHER] = createActionDispatcher(CLICK_BUTTON_ACTION)
allActions[CLICK_BUTTON_ACTION][STATE_HANDLER] = (payload = null) => {
        return (state) => {
            const newState = JSON.parse(JSON.stringify(state))
            newState[CLICKED_STATE_PROPERTY] = 'true'
            return newState;
        }
    }
