import React from 'react';
import { Link } from 'react-router-dom';
import styles from './BurguerOptions.module.css';
import commonStyles from '../../Navbar.module.css'
import { connect } from 'react-redux';

import { ACTION_DISPATCHER, } from '../../../../actions/constants';  
import {ICON_HEADER_STATE_PROPERTY,
        SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY,
        SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY,
        SHOW_BURGUER_OPTIONS_STATE_PROPERTY,
        CLOSING_SECOND_HEADER_STATE_PROPERTY,
        YES_CLOSING_SECOND_HEADER_OPTION
        } from '../../../../reducer/constants';
import {CLOSED_ICON_HEADER_OPTION,
        SHOW_ON_BACK_TO_MENU_OPTION,
        CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION,
        SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION,
        SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION,
        SHOW_OFF_SECONDARY_NAVIGATION_OPTION,
        SHOW_ON_BURGUER_OPTIONS,
        SHOW_OFF_BURGUER_OPTIONS} from '../../../../reducer/constants';
import {ICON_HEADER_ACTION,
        SHOW_BACK_TO_MENU_ACTION,
        SECONDARY_NAV_WRAPPER_ACTION,
        SHOW_SECONDARY_NAV_ACTION,
        SHOW_BURGUER_ACTION,
        CLOSING_SECOND_HEADER_ACTION} from '../../../../actions/constants';
import { allActions } from '../../../../actions';

import {PATH_ABOUTUS,
        PATH_ACCESORIES,
        PATH_HELPECENTER} from '../../../constants.js'



const displayBurguerOption = {}
displayBurguerOption[SHOW_ON_BURGUER_OPTIONS] = {}
displayBurguerOption[SHOW_OFF_BURGUER_OPTIONS] ={display: 'none'}
const styleBurguerOptions={}
styleBurguerOptions[SHOW_ON_BURGUER_OPTIONS] = styles.burguerOptionsOpened
styleBurguerOptions[SHOW_OFF_BURGUER_OPTIONS] = styles.hideElement

const arrowIconHover = (enable = 'off') => {
    const hoverArrowIcon = {}
    hoverArrowIcon['arrow']= {}
    hoverArrowIcon['arrow']['on'] = 'white'
    hoverArrowIcon['arrow']['off'] = '#FE3B1F'

    hoverArrowIcon['circle'] = {}
    hoverArrowIcon['circle']['on'] = '#FE3B1F'
    hoverArrowIcon['circle']['off'] = 'white'
    const icon = <svg width="28px" height="28px" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <circle cx="14" cy="14" r="14" fill={hoverArrowIcon['circle'][enable]}/>
                        <path d="M20.9122 13.7583L15.275 7.80857C15.1386 7.66722 14.9054 7.666 14.7739 7.79222C14.6405 7.92039 14.6306 8.15548 14.7574 8.29L19.8331 13.6492H6.65241C6.45776 13.6492 6.29999 13.8059 6.29999 13.9992C6.29999 14.1926 6.45776 14.3493 6.65241 14.3493H19.8331L14.7574 19.7085C14.6306 19.843 14.6385 20.0803 14.7739 20.2063C14.9093 20.3322 15.1275 20.3354 15.275 20.1899L20.9122 14.2402C21.0462 14.0764 21.011 13.8757 20.9122 13.759V13.7583Z" fill={hoverArrowIcon['arrow'][enable]}/>
                 </svg>
    return icon;
}
const textElementLink = 'text'
const arrowElementLink = 'arrow'
const productOptionId = 'productOptionId'
const servicesOptionId = 'servicesOptionId'
const ProductOrServiceLinkState = {}
ProductOrServiceLinkState[productOptionId] = {}
ProductOrServiceLinkState[productOptionId][SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION]= {}
ProductOrServiceLinkState[productOptionId][SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION][arrowElementLink] = arrowIconHover ('on')
ProductOrServiceLinkState[productOptionId][SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION][textElementLink] = {color: '#FE3B1F', overflow: 'hidden',whiteSpace: 'nowrap', textOverflow: 'ellipsis'}

ProductOrServiceLinkState[productOptionId][SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION]= {}
ProductOrServiceLinkState[productOptionId][SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION][arrowElementLink] = arrowIconHover()
ProductOrServiceLinkState[productOptionId][SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION][textElementLink] = { overflow: 'hidden',whiteSpace: 'nowrap', textOverflow: 'ellipsis' }

ProductOrServiceLinkState[productOptionId][SHOW_OFF_SECONDARY_NAVIGATION_OPTION] =  {}
ProductOrServiceLinkState[productOptionId][SHOW_OFF_SECONDARY_NAVIGATION_OPTION][arrowElementLink] = arrowIconHover ()
ProductOrServiceLinkState[productOptionId][SHOW_OFF_SECONDARY_NAVIGATION_OPTION][textElementLink] = { overflow: 'hidden',whiteSpace: 'nowrap', textOverflow: 'ellipsis'}

ProductOrServiceLinkState[servicesOptionId] = {}
ProductOrServiceLinkState[servicesOptionId][SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION]= {}
ProductOrServiceLinkState[servicesOptionId][SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION][arrowElementLink] = arrowIconHover ()
ProductOrServiceLinkState[servicesOptionId][SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION][textElementLink] = { overflow: 'hidden',whiteSpace: 'nowrap', textOverflow: 'ellipsis'}


ProductOrServiceLinkState[servicesOptionId][SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION]= {}
ProductOrServiceLinkState[servicesOptionId][SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION][arrowElementLink] = arrowIconHover('on')
ProductOrServiceLinkState[servicesOptionId][SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION][textElementLink] = {color: '#FE3B1F', overflow: 'hidden',whiteSpace: 'nowrap', textOverflow: 'ellipsis'}

ProductOrServiceLinkState[servicesOptionId][SHOW_OFF_SECONDARY_NAVIGATION_OPTION] =  {}
ProductOrServiceLinkState[servicesOptionId][SHOW_OFF_SECONDARY_NAVIGATION_OPTION][arrowElementLink] = arrowIconHover ()
ProductOrServiceLinkState[servicesOptionId][SHOW_OFF_SECONDARY_NAVIGATION_OPTION][textElementLink] = { overflow: 'hidden',whiteSpace: 'nowrap', textOverflow: 'ellipsis'}



const productsOrServices = {}
productsOrServices[productOptionId] = SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION
productsOrServices[servicesOptionId] = SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION


export function BurguerOptions(props) {

    
    function linkSelected()
    {
        props.setCloseSecondHeader(YES_CLOSING_SECOND_HEADER_OPTION)
        setTimeout(()=>{
            props.setIconHeader(CLOSED_ICON_HEADER_OPTION)
            props.setShowBurguerOptions(SHOW_OFF_BURGUER_OPTIONS)
            props.setSecondaryNav(SHOW_OFF_SECONDARY_NAVIGATION_OPTION)
            props.setSecondaryNavWrapper(CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION)
        }
        ,200)
        
        
    }
    function servicesOrProducts(event)
    {
        const optionSelected = productsOrServices[event.currentTarget.id]

        if(window.innerWidth>=768)
        {
            props.setSecondaryNav(optionSelected)
        }
        else if (window.innerWidth<768)
        {
            props.setShowBurguerOptions(SHOW_OFF_BURGUER_OPTIONS)
            props.setShowBackToMenu(SHOW_ON_BACK_TO_MENU_OPTION)
            props.setSecondaryNav(optionSelected)
        }
    }
    return (
        <div style={{position:'fixed',top:'88px', zIndex:'5', ...displayBurguerOption[props.showBurguerOptions]}} className={`${commonStyles.wrapperBurguerMenuOptionSecondaryNav} ${styles.wrapperBurguerOptions}`} >
            <div className={styleBurguerOptions[props.showBurguerOptions]}>
                <div  >     
                    <Link to={PATH_ABOUTUS}  className={styles.optionBurguerLink} onClick={linkSelected}>
                        <span>Sobre nosotros</span>
                    </Link>
                </div>
                <div  id={productOptionId} onClick={servicesOrProducts} className={styles.optionBurguerLink} style={ProductOrServiceLinkState[productOptionId][props.secondaryNav][textElementLink]}> 
                    
                        <span>Productos</span>
                        <div style={{width: '28px', height: '28px'}}>
                        {ProductOrServiceLinkState[productOptionId][props.secondaryNav][arrowElementLink]}
                        </div>
                 
                </div>
                <div  className={styles.optionBurguerLink} id={servicesOptionId} onClick={servicesOrProducts} style={ProductOrServiceLinkState[servicesOptionId][props.secondaryNav][textElementLink]}>
                  
                        <span>Servicios</span>
                        <div style={{width: '28px', height: '28px'}}>
                        {ProductOrServiceLinkState[servicesOptionId][props.secondaryNav][arrowElementLink]}
                        </div>
                </div>
                <div >
                    <Link to={PATH_ACCESORIES} className={styles.optionBurguerLink} onClick={linkSelected}>
                        <span>Accesorios</span>
                    </Link>
                </div>
                <div >
                    <Link to={PATH_HELPECENTER} className={styles.optionBurguerLink} onClick={linkSelected}>
                        <span>Centro de ayuda</span>
                    </Link>
                </div> 
            </div>
        </div>
    )
}


const mapStateToProps = (state) => {
    return {
    
      iconHeader: state[ICON_HEADER_STATE_PROPERTY],
      secondaryNavWrapper: state[SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY],
      secondaryNav: state[SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY],
      showBurguerOptions: state[SHOW_BURGUER_OPTIONS_STATE_PROPERTY],
      closeSecondHeader : state[CLOSING_SECOND_HEADER_STATE_PROPERTY]              
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setIconHeader: (payload) => dispatch(allActions[ICON_HEADER_ACTION][ACTION_DISPATCHER](payload)),
      setShowBackToMenu: (payload) => dispatch(allActions[SHOW_BACK_TO_MENU_ACTION][ACTION_DISPATCHER](payload)),
      setSecondaryNavWrapper: (payload) => dispatch(allActions[SECONDARY_NAV_WRAPPER_ACTION][ACTION_DISPATCHER](payload)),
      setSecondaryNav: (payload) => dispatch(allActions[SHOW_SECONDARY_NAV_ACTION][ACTION_DISPATCHER](payload)),
      setShowBurguerOptions: (payload) => dispatch(allActions[SHOW_BURGUER_ACTION][ACTION_DISPATCHER](payload)),
      setCloseSecondHeader: (payload) => dispatch(allActions[CLOSING_SECOND_HEADER_ACTION][ACTION_DISPATCHER](payload)),
    }
  }
 export default connect(mapStateToProps, mapDispatchToProps)(BurguerOptions);