import React, {useId} from 'react';
import styles from './CounterButton.module.css'
import { connect } from 'react-redux';

const minusIcon = <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M16 7.99916C16 8.20745 15.8317 8.37841 15.6207 8.37841H8.37934L8 8.37569H7.62066H0.379336C0.170991 8.37569 0 8.20739 0 7.99644C0 7.78549 0.168336 7.61719 0.379336 7.61719H7.62384H8L8.38233 7.62263H15.6268C15.8297 7.61979 16 7.79086 16 7.99916Z" fill="black"/>
                  </svg>

const plusIcon = <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M16 7.99864C16 8.20694 15.8317 8.37789 15.6207 8.37789H8.37934V15.6208C8.37934 15.829 8.211 16 8 16C7.789 16 7.62066 15.8317 7.62066 15.6208V8.37517H0.379336C0.170991 8.37517 0 8.20688 0 7.99592C0 7.78497 0.168336 7.61667 0.379336 7.61667H7.62384L7.62366 0.37925C7.62366 0.170953 7.792 0 8.003 0C8.214 0 8.38233 0.168298 8.38233 0.37925V7.62211H15.6268C15.8297 7.61928 16 7.79034 16 7.99864Z" fill="black"/>
                 </svg>
export function CounterButton (props)
{
    const plusIconId = useId()
    const minusIconId = useId()
    const [counter, setCounter] = React.useState(0)

    const clickHandler = (e) => {
        e.preventDefault()
        if( e.currentTarget.id === plusIconId) setCounter( counter + 1)
        else if (e.currentTarget.id === minusIconId)
        {
            if (counter !== 0) setCounter( counter - 1)
        }
    }
    return (
        <div className={styles.wrapperCounter}>

            <div className={styles.svgContainer} id={minusIconId} onClick={clickHandler}>
                {minusIcon}
            </div>
            <div className={styles.numberContainer}>{counter}</div>
            
            <div className={styles.svgContainer} id={plusIconId} onClick={clickHandler}>
                {plusIcon}
            </div>

        </div>
    )

}