import React from 'react';
import { connect } from 'react-redux';
import styles from './AlsoInterestProductPage.module.css'
import { CardsProductAlsoInterest } from './subcomponents/CardsProductAlsoInterest/CardsProductAlsoInterest';

export function AlsoInterestProductPage({titleAlsoInterest,otherProducts }) 
{
    const otherProductsTitles = Object.keys(otherProducts)
    const CardsProducts = []
    for (var i=0; i<otherProductsTitles.length; i++)
    {
        CardsProducts.push(<CardsProductAlsoInterest  key = {'Card-Product' + i}{...otherProducts[otherProductsTitles[i]]}></CardsProductAlsoInterest>)
    }

    return (
        <div className={styles.wrapper}>
            <h1 className={styles.titleAlsoInterest}> {titleAlsoInterest}</h1>
            <div className={`${styles.wrapperCarrousel}`}>
                {CardsProducts}
            </div>
        </div>
    )
}