import React from 'react';
import styles from './ButtonColorSelector.module.css'
import { connect } from 'react-redux';

const selected = 'selected'
const notSelected = 'notSelected'

const buttonContainer = 'buttonContainer'
const buttonText = 'buttonText'
const styleSelection = {}
styleSelection[selected] = {}
styleSelection[selected][buttonContainer] = styles.buttonContainerSelected
styleSelection[selected][buttonText] = styles.buttonTextSelected

styleSelection[notSelected] = {}
styleSelection[notSelected][buttonContainer] = ''
styleSelection[notSelected][buttonText] = ''

export function ButtonColorSelector (props)
{
    const [selection, setSelection] = React.useState(notSelected)
    const clickHandler = (e) =>{
        e.preventDefault()
        props.onClick(e)
        if (selection === selected) setSelection(notSelected)
        else if (selection === notSelected) setSelection(selected)
    }
    return (
        <div  className={`${styles.buttonContainer} ${styleSelection[selection][buttonContainer]}`} onClick={clickHandler} id={props.id}>
            <div className={styles.buttonCircleContainer}>
                <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <circle cx="8" cy="8" r="8" fill={props.colorbuttonColor}/>
                </svg>
            </div>
            <div className={`${styles.buttonText} ${styleSelection[selection][buttonText]}`} >
                {props.buttonColorText}
            </div>

           
        </div>
    )

}