import React from 'react';
import styles from './Navbar.module.css';
import { connect } from 'react-redux';
import { ACTION_DISPATCHER, CLOSING_SECOND_HEADER_ACTION, } from '../../actions/constants';  
import { ICON_HEADER_STATE_PROPERTY,
        SHOW_BURGUER_OPTIONS_STATE_PROPERTY,
        SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY,
        SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY, 
        SHOW_BACK_TO_MENU_STATE_PROPERTY,
        CLOSING_SECOND_HEADER_STATE_PROPERTY,
        OPEN_SECONDARY_WRAPPER_NAVIGATION_OPTION} from '../../reducer/constants';
import {CLOSED_ICON_HEADER_OPTION,
        OPENED_ICON_HEADER_OPTION,
        CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION,
        SHOW_OFF_SECONDARY_NAVIGATION_OPTION,
        SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION,
        SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION,
        NOT_CLOSING_SECOND_HEADER_OPTION,
        YES_CLOSING_SECOND_HEADER_OPTION,
        SHOW_OFF_BURGUER_OPTIONS,
        SHOW_ON_BURGUER_OPTIONS,
        SHOW_ON_BACK_TO_MENU_OPTION,
        SHOW_OFF_BACK_TO_MENU_OPTION
        } from '../../reducer/constants';
import {SECONDARY_NAV_WRAPPER_ACTION,
        SHOW_SECONDARY_NAV_ACTION} from '../../actions/constants';
import { allActions } from '../../actions';
import PrimaryHeader from './subcomponents/PrimaryHeader/PrimaryHeader';
import SecondHeader from './subcomponents/SecondHeader/SecondHeader';








const styleWrapper = {}
styleWrapper[CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION] = ''
styleWrapper[OPEN_SECONDARY_WRAPPER_NAVIGATION_OPTION] = styles.wrapperWhenShowSecondaryHeader


const ClosingWrapperAnimation = {}
ClosingWrapperAnimation[NOT_CLOSING_SECOND_HEADER_OPTION] = ''
ClosingWrapperAnimation[YES_CLOSING_SECOND_HEADER_OPTION] = styles.wrapperBurguerMenuOptionSecondaryNavClosing

export function Nav(props) {
    

    function onMouseLeaveHeader (event){
        if (window.innerWidth>1024)
        {
            
          if(props.closeSecondHeader !== YES_CLOSING_SECOND_HEADER_OPTION) props.setCloseSecondHeader(YES_CLOSING_SECOND_HEADER_OPTION)
            setTimeout(()=>{
             if (props.secondaryNavWrapper !== CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION)  props.setSecondaryNavWrapper(CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION)
             if (props.showSecondaryNav !== SHOW_OFF_SECONDARY_NAVIGATION_OPTION) props.setShowSecondaryNav(SHOW_OFF_SECONDARY_NAVIGATION_OPTION)
            },300)
        }
        
    }
  
    return (
    <div>
      <div className={`${styles.wrapper}`} >
            <PrimaryHeader id='backHeader2'/>
            
        </div>

        <div  onMouseLeave={onMouseLeaveHeader}>
        <div className={`${styles.wrapper} ${styleWrapper[props.secondaryNavWrapper]}  ${ClosingWrapperAnimation[props.closeSecondHeader]}`}>
            <PrimaryHeader id='backHeader2'/>
            {/* <SecondHeader /> */}
        </div>
        <div className={` ${ClosingWrapperAnimation[props.closeSecondHeader]}`} >
            <SecondHeader />
        </div>
        </div>
    </div>
    )
  };

  const mapStateToProps = (state) => {
    return {
      iconHeader: state[ICON_HEADER_STATE_PROPERTY],
      showBackToMenu: state[SHOW_BACK_TO_MENU_STATE_PROPERTY],
      secondaryNavWrapper: state[SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY],
      showSecondaryNav: state[SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY],
      showBurguerOptions: state[SHOW_BURGUER_OPTIONS_STATE_PROPERTY],
      closeSecondHeader : state[CLOSING_SECOND_HEADER_STATE_PROPERTY]       
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setSecondaryNavWrapper: (payload) => dispatch(allActions[SECONDARY_NAV_WRAPPER_ACTION][ACTION_DISPATCHER](payload)),
      setShowSecondaryNav: (payload) => dispatch(allActions[SHOW_SECONDARY_NAV_ACTION][ACTION_DISPATCHER](payload)),
      setCloseSecondHeader: (payload) => dispatch(allActions[CLOSING_SECOND_HEADER_ACTION][ACTION_DISPATCHER](payload)),
    }
  }
  export default connect(mapStateToProps, mapDispatchToProps)(Nav);
  //export default Nav;