import React, {useId} from 'react';
import { connect } from 'react-redux';
import styles from './TermsAndConditions.module.css'
import commonStyles from '../../App.module.css'
import { PreReserveComponent } from './subcomponents/PreReserveComponent/PreReserveComponent.js';
import { BuyComponent } from './subcomponents/BuyComponent/BuyComponent';
import { RentAndSuscription } from './subcomponents/RentAndSuscriptionComponent/RentAndSuscription';
import { MotoSharing } from './subcomponents/MotoSharing/MotoSharing';
import  ButtonNavigationTermsAndConditions  from './subcomponents/ButtonNavigationTermsAndConditions/ButtonNavigationTermsAndConditions';
import {PRE_RESERVE_OPTION,
        BUY_OPTION,
        RENT_AND_SUSCRIPTION_OPTION,
        MOTOSHARING_OPTION} from '../../reducer/constants';
import { TERMS_AND_CONDITIONS_NAV_STATE_PROPERTY } from '../../reducer/constants';
import { TERMS_AND_CONDITIONS_NAV_ACTION, ACTION_DISPATCHER } from '../../actions/constants';
import { StickyNavigation } from './subcomponents/StickyNavigation/StickyNavigation';
import { allActions } from '../../actions';
const TERMS_AND_CONDITIONS_TO_SHOW = {}
TERMS_AND_CONDITIONS_TO_SHOW[PRE_RESERVE_OPTION] = <PreReserveComponent/>
TERMS_AND_CONDITIONS_TO_SHOW[BUY_OPTION] = <BuyComponent/>
TERMS_AND_CONDITIONS_TO_SHOW[RENT_AND_SUSCRIPTION_OPTION] = <RentAndSuscription/>
TERMS_AND_CONDITIONS_TO_SHOW[MOTOSHARING_OPTION] = <MotoSharing/>
export function TermsAndConditions(props) {
   
    return (
        <div className={styles.wrapper}>
            <div className={styles.emptyDiv}>
           </div>
           <StickyNavigation/>
           <div className={`${styles.titleWrapper} ${commonStyles.wrapperMainWindow}`}>
                <div className={styles.title}>
                    Términos y condiciones
                </div>
                <div className={styles.updateText}>
                    Fecha de actualización: 30/07/21
                </div>
           </div>
           <div className={`${styles.accordionWrapper} ${commonStyles.wrapperMainWindow}`}>
               <div className={styles.accordionsubWrapper}>
                    {TERMS_AND_CONDITIONS_TO_SHOW[props.termsAndConditions]}
               </div>

           </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
    
      termsAndConditions: state[TERMS_AND_CONDITIONS_NAV_STATE_PROPERTY],
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setTermsAndConditions: (payload) => dispatch(allActions[TERMS_AND_CONDITIONS_NAV_ACTION][ACTION_DISPATCHER](payload)),
    }
  }
 export default connect(mapStateToProps, mapDispatchToProps)(TermsAndConditions);