import { initialState } from "./constants";
import { allActions } from "../actions";
import {STATE_HANDLER} from "../actions/constants"; 
const all = (state = initialState, action) => {

  
  if (allActions.hasOwnProperty(action.type)) return allActions[action.type][STATE_HANDLER] (action.payload)(state)
  
  else return state
}

export default all;