import React, {useEffect} from 'react';
import { connect } from 'react-redux';
import styles from './ProductColorSelector.module.css'
import { ButtonColorSelector } from './subcomponents/ButtonColorSelector';

const  separateIntoSpans = (text, wordToHighlight) => {
    
    const separateText = text.split(wordToHighlight)
    const spansToAppend = []
    for (var i=0; i<separateText.length; i++)
    {
     

        var spanElementRegular = <span key= {`RegularSpan ${i}` } className={styles.regularSpanPrimaryDescription}>{separateText[i]}</span>
        spansToAppend.push(spanElementRegular)

        var spanElementHighlighted = <span key= {`HighlightSpan ${i}` } className={styles.HighlightSpanPrimaryDescription}> {wordToHighlight}</span>
        spansToAppend.push(spanElementHighlighted)
    }
    if (separateText.length>1) spansToAppend.pop()
    
    return spansToAppend


}
export function ProductColorSelector ({images, colorOptions, colorOptionsLabels, titleText, titleTextHighlight}) {
    const length = images.length
    const [optionSelected, setOptionSelected] = React.useState('Image-Color-Selection-0');
    const [showImage, setShowImage] = React.useState('0');

    const spans = separateIntoSpans(titleText,titleTextHighlight)
    const [maxWidthImage, setMaxWidthImage] = React.useState('100%');
    function getImgSize(imgSrc) {
        var newImg = new Image();
        newImg.onload = function() 
        {
            var width = newImg.width;
            setMaxWidthImage(width+'px')
        }
        newImg.src = imgSrc; // this must be done AFTER setting onload
    }
    const Images = []
    const ButtonsColors = []
    const clickHandler = (e) => 
    {    e.preventDefault()
        if (e.currentTarget.id !== optionSelected) 
        {
            document.getElementById(optionSelected).click()
            setOptionSelected(e.currentTarget.id)
        }
        
        
    }
    for (var i = 0; i<length; i++)
    {
        Images.push(<img id={'Image-Color-Selected-'+i} key = {'Image-key-Color-Selected-'+i} style={{maxWidth:maxWidthImage}} src={images[i]} alt="Scooter" width={'100%'} height={'100%'}></img>)
        ButtonsColors.push(<ButtonColorSelector onClick={clickHandler} key = {'Button-key-Color-Selection-'+i}id={'Image-Color-Selection-'+i} buttonColorText={colorOptionsLabels[i]} colorbuttonColor={colorOptions[i]}></ButtonColorSelector>)
    }
    React.useEffect(()=>{
        document.getElementById('Image-Color-Selection-0').click()
    },[])
    React.useEffect(()=>{
        const index = optionSelected.split('Image-Color-Selection-')
        setShowImage(index[1])
    },[optionSelected])
    React.useEffect(()=>{
        getImgSize(images[showImage])
    },[showImage,images])
   
    return (
        <div className={styles.wrapper}>
            <div className={styles.title}>{spans}</div>
            <div className={styles.imageContainer}>
                {Images[showImage]}
            </div>
            <div className={styles.buttonContainer}>
                {ButtonsColors}
            </div>
        </div>
    )
}