import React from 'react';

import styles from './SecondaryNavigation.module.css';
import commonStyles from '../../Navbar.module.css';
import { connect } from 'react-redux';
import { useEffect } from 'react';
import { Products } from './subcomponents/Products/Products';
import { Services } from './subcomponents/Services/Services';


import {SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY} from '../../../../reducer/constants';
import {SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION,
        SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION,
        SHOW_OFF_SECONDARY_NAVIGATION_OPTION} from '../../../../reducer/constants';



const showMainSecondary = {}
showMainSecondary[SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION] = {display: 'initial', animation: ''}
showMainSecondary[SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION] = {display: 'initial', animation: ''}
showMainSecondary[SHOW_OFF_SECONDARY_NAVIGATION_OPTION]  = {display: 'none', animation: ''}


const contentToShow = {}
contentToShow[SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION] = <Products key='products-cards-secondary-nav'></Products> 
contentToShow[SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION] = <Services key='services-cards-secondary-nav'></Services> 

contentToShow[SHOW_OFF_SECONDARY_NAVIGATION_OPTION]  = <div></div>

export function SecondaryNavigation (props)
{
  const [inlineStyle, setInlineStyle] = React.useState({animation:'none', display: 'none'});

  useEffect(() => {
    setInlineStyle({animation:'none', display: 'none'})
    setTimeout(()=> {
      setInlineStyle({...showMainSecondary[props.showSecondaryNav]})
    }, 100)
           
  }, [props.showSecondaryNav]);
  
  
    return (
        <div className={`${commonStyles.wrapperBurguerMenuOptionSecondaryNav} ${styles.wrapperSecondaryNav} `}>
           <div className={`${styles.mainSecondary} `} style={inlineStyle}>
           <div className={styles.bottom2}></div>
              
             
              {contentToShow[props.showSecondaryNav]}
             
          </div> 
        </div>
    )

}
const mapStateToProps = (state) => {
    return {
    
      showSecondaryNav: state[SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY],         
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
    }
  }
 export default connect(mapStateToProps, mapDispatchToProps)(SecondaryNavigation);