import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import styles from './PrimaryHeader.module.css';
import { connect } from 'react-redux';
import {PATH_ABOUTUS,
        PATH_ACCESORIES,
        PATH_CART,
        PATH_HELPECENTER,
        PATH_PRODUCTS,
        PATH_SERVICES} from '../../../constants.js'
import { useEffect } from 'react';
import { ACTION_DISPATCHER, } from '../../../../actions/constants';  
import { ICON_HEADER_STATE_PROPERTY,
        SHOW_BURGUER_OPTIONS_STATE_PROPERTY,
        SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY,
        SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY, 
        SHOW_BACK_TO_MENU_STATE_PROPERTY,
        SELECTED_LINK_HEADER_NAVIGATION_STATE_PROPERTY,
        CLOSING_SECOND_HEADER_STATE_PROPERTY} from '../../../../reducer/constants';
import {OPEN_SECONDARY_WRAPPER_NAVIGATION_OPTION,
        CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION,
        SHOW_OFF_SECONDARY_NAVIGATION_OPTION,
        SHOW_OFF_BACK_TO_MENU_OPTION,
        SHOW_ON_BACK_TO_MENU_OPTION,
        SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION,
        SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION,
        SHOW_ON_BURGUER_OPTIONS,
        SHOW_OFF_BURGUER_OPTIONS,
        YES_CLOSING_SECOND_HEADER_OPTION,
        NOT_CLOSING_SECOND_HEADER_OPTION} from '../../../../reducer/constants';
import {ICON_HEADER_ACTION,
        SHOW_BACK_TO_MENU_ACTION,
        SECONDARY_NAV_WRAPPER_ACTION,
        SHOW_SECONDARY_NAV_ACTION,
        SHOW_BURGUER_ACTION,
        SELECTED_LINK_HEADER_ACTION,
        CLOSING_SECOND_HEADER_ACTION} from '../../../../actions/constants';
import { allActions } from '../../../../actions';
import HomeIcon from '../HomeIcon/HomeIcon';
import BurguerIcon from '../BurguerIcon/BurguerIcon';
import Cart from '../Cart/Cart';

const PATH_HOME = '/'
const initialStylingText = {}

initialStylingText[PATH_ABOUTUS] = styles.textDefaultNav
initialStylingText[PATH_PRODUCTS] = styles.textDefaultNav
initialStylingText[PATH_SERVICES] = styles.textDefaultNav
initialStylingText[PATH_ACCESORIES] = styles.textDefaultNav
initialStylingText[PATH_HELPECENTER] = styles.textDefaultNav
initialStylingText[PATH_CART] = styles.textDefaultNav

const ServiceId = 'serviceLinkId'
const ProductsId = 'productsLinkId'
const mainHeaderId = 'mainHeader'

const stylePrimaryHeader = styles.backgroundDivLocation

const contentToDisplaySecondaryNav = {}
contentToDisplaySecondaryNav[ServiceId] = SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION
contentToDisplaySecondaryNav[ProductsId] = SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION

export function PrimaryHeader(props) {
    
    const [styleText, setStyleText] = React.useState({...initialStylingText});
    const [styleBackground, setStyleBackground] = React.useState({});
    
    const location = useLocation();
    useEffect(() => {
        
        
        const objStyle = {...initialStylingText}
        objStyle[location.pathname] = styles.textSelectedNav
        setStyleText({...objStyle})
        if (location.pathname === PATH_HOME)
        {
            if(props.showBurguerOptions === SHOW_ON_BURGUER_OPTIONS || props.showBackToMenu === SHOW_ON_BACK_TO_MENU_OPTION) setStyleBackground('')
            else setStyleBackground(stylePrimaryHeader) 
    
        }
         
        else setStyleBackground('')
      }, [location, props.showBurguerOptions, props.showBackToMenu]);

    function onMouseOverProductOrService (event){
        event.preventDefault()
        const secondaryNavHover = contentToDisplaySecondaryNav[event.currentTarget.id]
      
        if (props.secondaryNavWrapper !== OPEN_SECONDARY_WRAPPER_NAVIGATION_OPTION) props.setSecondaryNavWrapper(OPEN_SECONDARY_WRAPPER_NAVIGATION_OPTION)
        if (props.showSecondaryNav !== secondaryNavHover) props.setShowSecondaryNav(secondaryNavHover)
        if(props.closeSecondHeader !== NOT_CLOSING_SECOND_HEADER_OPTION) props.setCloseSecondHeader(NOT_CLOSING_SECOND_HEADER_OPTION)
    
    } 
    function onMouseLeaveHeader (event){
        if (window.innerWidth>1024)
        {
            if(props.closeSecondHeader !== YES_CLOSING_SECOND_HEADER_OPTION) props.setCloseSecondHeader(YES_CLOSING_SECOND_HEADER_OPTION)
            setTimeout(()=>{
             if (props.secondaryNavWrapper !== CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION)  props.setSecondaryNavWrapper(CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION)
             if (props.showSecondaryNav !== SHOW_OFF_SECONDARY_NAVIGATION_OPTION) props.setShowSecondaryNav(SHOW_OFF_SECONDARY_NAVIGATION_OPTION)
            },300)
            
        }
        
    }  
  
    return (
            <div className={`${styles.backgroundDiv} ${styleBackground}`} id={mainHeaderId}>
            
                <BurguerIcon onMouseOver={onMouseLeaveHeader}/>
                <HomeIcon onMouseOver={onMouseLeaveHeader}/>
                <div className={styles.divLinks}>
                    <div onMouseOver={onMouseLeaveHeader}>
                        <Link to={PATH_ABOUTUS} className={styleText[PATH_ABOUTUS]}>
                            <span>Sobre nosotros</span>
                        </Link>
                    </div>
                    <div onMouseOver={onMouseOverProductOrService} id={ProductsId} >
                        <Link to={PATH_PRODUCTS} className={styleText[PATH_PRODUCTS]}>
                            <span>Productos</span>
                        </Link>
                    
                    </div>
                    <div onMouseOver={onMouseOverProductOrService} id={ServiceId}>
                        <Link to={PATH_SERVICES} className={styleText[PATH_SERVICES]}>
                            <span>Servicios</span>
                        </Link>
                    </div>   
                    <div onMouseOver={onMouseLeaveHeader}>
                        <Link to={PATH_ACCESORIES} className={styleText[PATH_ACCESORIES]}>
                            <span>Accesorios</span>
                        </Link>
                    </div>
                    <div onMouseOver={onMouseLeaveHeader}>
                        <Link to={PATH_HELPECENTER} className={styleText[PATH_HELPECENTER]}>
                            <span>Centro de ayuda</span>
                        </Link>
                    </div>           
                </div>
                <Cart onMouseOver={onMouseLeaveHeader}/>
            </div>
    )
  };

  const mapStateToProps = (state) => {
    return {
      iconHeader: state[ICON_HEADER_STATE_PROPERTY],
      showBackToMenu: state[SHOW_BACK_TO_MENU_STATE_PROPERTY],
      secondaryNavWrapper: state[SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY],
      showSecondaryNav: state[SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY],
      showBurguerOptions: state[SHOW_BURGUER_OPTIONS_STATE_PROPERTY],
      showLinkSelected:   state[SELECTED_LINK_HEADER_NAVIGATION_STATE_PROPERTY],
      closeSecondHeader: state[CLOSING_SECOND_HEADER_STATE_PROPERTY]  
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setIconHeader: (payload) => dispatch(allActions[ICON_HEADER_ACTION][ACTION_DISPATCHER](payload)),
      setShowBackToMenu: (payload) => dispatch(allActions[SHOW_BACK_TO_MENU_ACTION][ACTION_DISPATCHER](payload)),
      setSecondaryNavWrapper: (payload) => dispatch(allActions[SECONDARY_NAV_WRAPPER_ACTION][ACTION_DISPATCHER](payload)),
      setShowBurguer: (payload) => dispatch(allActions[SHOW_BURGUER_ACTION][ACTION_DISPATCHER](payload)),
      setShowSecondaryNav: (payload) => dispatch(allActions[SHOW_SECONDARY_NAV_ACTION][ACTION_DISPATCHER](payload)),
      setLinkSelected : (payload) => dispatch(allActions[SELECTED_LINK_HEADER_ACTION][ACTION_DISPATCHER](payload)),
      setCloseSecondHeader: (payload) => dispatch(allActions[CLOSING_SECOND_HEADER_ACTION][ACTION_DISPATCHER](payload)),
  
    }
  }
  export default connect(mapStateToProps, mapDispatchToProps)(PrimaryHeader);
  //export default Nav;