import React, {useEffect, useId} from 'react';
import { connect } from 'react-redux';
import styles from './CarrouselImages.module.css'

const ARROW_NAVIGATION = <svg width="100%" height="100%" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
<circle cx="14" cy="14" r="14" fill='white'/>
<path d="M20.9122 13.7583L15.275 7.80857C15.1386 7.66722 14.9054 7.666 14.7739 7.79222C14.6405 7.92039 14.6306 8.15548 14.7574 8.29L19.8331 13.6492H6.65241C6.45776 13.6492 6.29999 13.8059 6.29999 13.9992C6.29999 14.1926 6.45776 14.3493 6.65241 14.3493H19.8331L14.7574 19.7085C14.6306 19.843 14.6385 20.0803 14.7739 20.2063C14.9093 20.3322 15.1275 20.3354 15.275 20.1899L20.9122 14.2402C21.0462 14.0764 21.011 13.8757 20.9122 13.759V13.7583Z" fill='#FE3B1F' />
</svg>



export function CarrouselImages (props) {
    const images = props.images
    
    const lengthImages = images.length
    const previousNavigation = useId()
    const nextNavigation = useId()
    const imageCarrousel = useId()
    const [indexImage, setIndexImage] = React.useState(0);
    useEffect(() => {
		
		// Handler to call on window resize
		function handleResize() {
            setIndexImage(0)
		 
		  
		}
		// Add event listener
		window.addEventListener("resize", handleResize);
		// Call handler right away so state gets updated with initial window size
		handleResize();
		// Remove event listener on cleanup
		return () => window.removeEventListener("resize", handleResize);
	  }, [])
    useEffect(() => {
        const widthImageCarrousel = document.getElementById(imageCarrousel).scrollWidth / lengthImages
        document.getElementById(imageCarrousel).scrollTo(indexImage * widthImageCarrousel,0)
    }
    ,[indexImage])
    const clickAnchor = (event) =>{
        event.preventDefault()

        if (event.currentTarget.id === previousNavigation)
        {
            if (indexImage === 0) setIndexImage(lengthImages - 1)
            else setIndexImage(indexImage - 1)
        }
        else if (event.currentTarget.id === nextNavigation)
        {
            if (indexImage === (lengthImages - 1)) setIndexImage(0)
            else setIndexImage(indexImage + 1)
        }
    }
    const arrayImages= []
    for (var i=0 ; i< lengthImages; i++)
    {
        var keyImage = i + '-carrousel-image'
        arrayImages.push(<div key={keyImage} className={styles.carrouselImage} >
            <img src={images[i]} alt='mo-images' width={'100%'} height={'100%'} style={{objectFit: 'cover'}}></img>
        </div>)
    }
   
    return (
        <div className={styles.carrouselWrapper}>
            <div className={styles.navigationArrowsContainers}>
                <div className={`${styles.arrowContainer} ${styles.leftArrowContainer}`} onClick={clickAnchor} id={previousNavigation}>
                 {ARROW_NAVIGATION}
                </div>
                <div className={`${styles.arrowContainer} `} onClick={clickAnchor} id={nextNavigation}>
                     {ARROW_NAVIGATION} 
                </div>
            </div>
            <div className={styles.carrouselviewport} id={imageCarrousel}>
                {arrayImages}
            </div>
        </div>
    )
}