import React, { useId, useEffect } from 'react';
import styles from './MenuServiceCardComponent.module.css';
import { PrimaryButton } from '../buttons/primaryButton/primaryButton.js';
const viewportType = {}
viewportType['L'] = {}
viewportType['L']['nCol'] = 12
viewportType['L']['gap'] = 32

viewportType['M'] = {}
viewportType['M']['nCol'] = 6
viewportType['M']['gap'] = 32

viewportType['S'] = {}
viewportType['S']['nCol'] = 4
viewportType['S']['gap'] = 20

function getTypeOfViewport (width)
{
    var viewportTag = 'none'
    if (width>1024) viewportTag = 'L'
    else if (width>768) viewportTag = 'M'
    else if (width<= 768) viewportTag = 'S'
    return viewportTag
}

function getWidthOneColumn (width) {
    const viewportTag = getTypeOfViewport(width)
    const oneColumnWidth = (width - viewportType[viewportTag]['gap'] * (viewportType[viewportTag]['nCol'] + 1)) / (viewportType[viewportTag]['nCol'])
    return oneColumnWidth;
}
const cardViewportType = {}
cardViewportType['L'] = {}
cardViewportType['L']['nCol'] = 3


cardViewportType['M'] = {}
cardViewportType['M']['nCol'] = 3


cardViewportType['S'] = {}
cardViewportType['S']['nCol'] = 4

function getWidthCard (width)
{
    const viewportTag = getTypeOfViewport(width)
    const oneColumnWidth = getWidthOneColumn (width)
    const cardWidth = oneColumnWidth * cardViewportType[viewportTag]['nCol'] + viewportType[viewportTag]['gap'] * (cardViewportType[viewportTag]['nCol'] - 1)
    return cardWidth

}

function getTextWidth(text, font) {
    // re-use canvas object for better performance
    const canvas = getTextWidth.canvas || (getTextWidth.canvas = document.createElement("canvas"));
    const context = canvas.getContext("2d");
    context.font = font;
    const metrics = context.measureText(text);
    return metrics.width;
  }
  
  function getCssStyle(element, prop) {
      return window.getComputedStyle(element, null).getPropertyValue(prop);
  }
  
  function getCanvasFont(el = document.body) {
    const fontWeight = getCssStyle(el, 'font-weight') || 'normal';
    const fontSize = getCssStyle(el, 'font-size') || '16px';
    const fontFamily = getCssStyle(el, 'font-family') || 'Times New Roman';
    
    return `${fontWeight} ${fontSize} ${fontFamily}`;
  }
export function MenuServiceCardComponent(props) {
    

    const id = useId()
    const cities = []
    if (Array.isArray(props.cities)) cities.push(...props.cities)
    const allCities = cities.map(city => {
        return  <div className={styles.chip_block} key={id + city} id={id +city}>
                    {city}
                </div>
    })
    const [chipsToShow, setChipsToShow] = React.useState(allCities);
   
    
    useEffect(() => {
        const widthDimensions = {}
        if (cities.length>0 )
        {
		    
            cities.forEach((city,index) => {
                widthDimensions[city] = {}
                widthDimensions[city].width = 20 + getTextWidth(city, getCanvasFont(document.getElementsByClassName(styles.chip_block)[0]));
                widthDimensions[city].element = allCities[index]
            })
        }
		// Handler to call on window resize
		function handleResize() {
        
            const cardWidth = getWidthCard (window.innerWidth) - 48
            const chipsToFill = []
            if (cities.length>0 )
            {
                var widthToFill = 0
                cities.forEach(city => {
                    widthToFill = widthToFill + widthDimensions[city].width + 16
                    if ((widthToFill + 39) > cardWidth) return
                     chipsToFill.push(widthDimensions[city].element)
                })
                if (chipsToFill.length < cities.length) 
                {

                    var numberOfPillsLeft = `+ ${(cities.length - chipsToFill.length)}`
                    var numberToShow =  <div className={styles.chip_block} key={id + 'numberChip'} >
                                            {numberOfPillsLeft}
                                        </div>
                    chipsToFill.push(numberToShow)
                } 
            
                setChipsToShow(chipsToFill)
         
            }
		}   
		// Add event listener
		window.addEventListener("resize", handleResize);
		// Call handler right away so state gets updated with initial window size
		handleResize();
		// Remove event listener on cleanup
		return () => window.removeEventListener("resize", handleResize);
	  }, []); // Empty array ensures that effect is only run on mount
    return (

               
                <div className={styles.card} id={id}> 
                    <div className={styles.cardTitle}>
                        {props.title}
                    </div>
                    <div className={styles.cardPrice}>
                        {props.price}
                    </div>
             
                    <div className={styles.chip_row} >
                        {chipsToShow}
                    </div>
                    <div className={styles.horizontanLine}>
                        
                    </div>
                    <PrimaryButton textButton='Saber más'/>
                </div>
    )
}



