import React from 'react';
import styles from './CardsProductAlsoInterest.module.css'
import { PromotionTag } from '../PromotionTag/PromotionTag';
import { SecondaryButton } from '../../../buttons/SecondaryButton/SecondaryButton';
import { PriceText } from '../PriceText/PriceText';
export function CardsProductAlsoInterest({title, imgSource, description, promotion, priceCrossOut, normalPrice}) 
{
    const [maxWidthImage, setMaxWidthImage] = React.useState('100%');
    function getImgSize(imgSrc) {
        var newImg = new Image();
        newImg.onload = function() 
        {
            var width = newImg.width;
            setMaxWidthImage(width+'px')
        }
        newImg.src = imgSrc; // this must be done AFTER setting onload
    }
    getImgSize(imgSource)
    var PromotionTagElement = null
    if (promotion=== 'true')  PromotionTagElement = <PromotionTag textPromotion={'Promoción'}></PromotionTag>
    return (
                <div className={styles.card}> 
                
                    <div className={styles.topContainer}>
                        <div className={styles.titleContainer}>
                            <div className={styles.cardTitle}>
                                    {title}
                            </div>
                            {PromotionTagElement}
                        </div>
                        <div className={styles.cardDescription}>
                            {description}
                        </div>
                    </div>
                    <div className={styles.bottomContainer}>
                        <div className={styles.imageContainer}>
                            <img style={{maxWidth:maxWidthImage}} src={imgSource} alt="Scooter" width={'100%'} height={'100%'}/>
                        </div>
                        <PriceText priceCrossOut={priceCrossOut} normalPrice={normalPrice}/>
                        <SecondaryButton textButton={'Explorar y comprar'}/>
                    </div>  
                </div>
    )
}