import React from 'react';
import { useEffect } from 'react';
import styles from './BackToMenu.module.css';
import { connect } from 'react-redux';
import { ACTION_DISPATCHER, } from '../../../../actions/constants';  
import { SHOW_BACK_TO_MENU_STATE_PROPERTY } from '../../../../reducer/constants';
import {CLOSED_ICON_HEADER_OPTION,
        SHOW_ON_BACK_TO_MENU_OPTION,
        SHOW_OFF_BACK_TO_MENU_OPTION,
        CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION,
        SHOW_OFF_SECONDARY_NAVIGATION_OPTION,
        SHOW_ON_BURGUER_OPTIONS,
        SHOW_OFF_BURGUER_OPTIONS} from '../../../../reducer/constants';
import {ICON_HEADER_ACTION,
        SHOW_BACK_TO_MENU_ACTION,
        SECONDARY_NAV_WRAPPER_ACTION,
        SHOW_SECONDARY_NAV_ACTION,
        SHOW_BURGUER_ACTION} from '../../../../actions/constants';
import { allActions } from '../../../../actions';

const styleBackToMenu = {}
styleBackToMenu[SHOW_OFF_BACK_TO_MENU_OPTION] = styles.hideElement
styleBackToMenu[SHOW_ON_BACK_TO_MENU_OPTION] = styles.showBackToMenuBurguer


export function BackToMenu(props) {
    
  
  useEffect(() => {
		
		// Handler to call on window resize
		function handleResize() {

		  // Set window width/height to state
          if  (window.innerWidth>=768)  
          {
            props.setShowBackToMenu(SHOW_OFF_BACK_TO_MENU_OPTION)
            props.setSecondaryNavWrapper(CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION)
            props.setShowBurguerOptions(SHOW_OFF_BURGUER_OPTIONS)
            props.setSecondaryNavigation(SHOW_OFF_SECONDARY_NAVIGATION_OPTION)
            props.setIconHeader(CLOSED_ICON_HEADER_OPTION)
          }
		  
		}
		// Add event listener
		window.addEventListener("resize", handleResize);
		// Call handler right away so state gets updated with initial window size
		handleResize();
		// Remove event listener on cleanup
		return () => window.removeEventListener("resize", handleResize);
	  }, [])
    function showOptionsMenuBurguer ()
    {
        props.setShowBackToMenu(SHOW_OFF_BACK_TO_MENU_OPTION)
        props.setSecondaryNavigation(SHOW_OFF_SECONDARY_NAVIGATION_OPTION)
        props.setShowBurguerOptions(SHOW_ON_BURGUER_OPTIONS)

    }
   
    return (      
            <div className={styleBackToMenu[props.showBackToMenuState]} onClick={showOptionsMenuBurguer}>
                <div className={styles.arrowIconBackToMenu}>
                    <svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <circle cx="14" cy="14" r="14" transform="rotate(-180 14 14)" fill="#FE3B1F"/>
                        <path d="M7.08772 14.2417L12.7249 20.1914C12.8613 20.3328 13.0946 20.334 13.226 20.2078C13.3595 20.0796 13.3693 19.8445 13.2425 19.71L8.16685 14.3508L21.3475 14.3508C21.5422 14.3508 21.7 14.1941 21.7 14.0008C21.7 13.8074 21.5422 13.6507 21.3475 13.6507L8.16685 13.6507L13.2425 8.29151C13.3693 8.157 13.3614 7.9197 13.226 7.79373C13.0906 7.66776 12.8724 7.66459 12.7249 7.81009L7.08772 13.7598C6.95378 13.9236 6.98892 14.1243 7.08772 14.241L7.08772 14.2417Z" fill="white"/>
                    </svg>
                </div>
                <div className={styles.textBackToMenu} >
                    Volver al menú
                </div>
            </div>
    )
  };
  


  const mapStateToProps = (state) => {
    return {
    
      showBackToMenuState: state[SHOW_BACK_TO_MENU_STATE_PROPERTY],
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setIconHeader: (payload) => dispatch(allActions[ICON_HEADER_ACTION][ACTION_DISPATCHER](payload)),
      setShowBackToMenu: (payload) => dispatch(allActions[SHOW_BACK_TO_MENU_ACTION][ACTION_DISPATCHER](payload)),
      setSecondaryNavWrapper: (payload) => dispatch(allActions[SECONDARY_NAV_WRAPPER_ACTION][ACTION_DISPATCHER](payload)),
      setSecondaryNavigation: (payload) => dispatch(allActions[SHOW_SECONDARY_NAV_ACTION][ACTION_DISPATCHER](payload)),
      setShowBurguerOptions: (payload) => dispatch(allActions[SHOW_BURGUER_ACTION][ACTION_DISPATCHER](payload)),
    }
  }
 export default connect(mapStateToProps, mapDispatchToProps)(BackToMenu);