import React from 'react';
import { connect } from 'react-redux';
import styles from './VideoButton.module.css';
const colorIcon = {}
colorIcon['onMouseOver'] = {}
colorIcon['onMouseOver']['circle'] = '#FE3B1F'
colorIcon['onMouseOver']['arrow'] = 'white'
colorIcon['onMouseOut'] = {}
colorIcon['onMouseOut']['circle'] = 'white'
colorIcon['onMouseOut']['arrow'] = '#FE3B1F'
export function VideoButton(props) {
    const [colorArrowIcon, setColorArrowIcon] = React.useState({...colorIcon['onMouseOut']});
    function onMouseOverButton ()
    {
        setColorArrowIcon({...colorIcon['onMouseOver']})
    }
    function onMouseOutButton ()
    {
        setColorArrowIcon({...colorIcon['onMouseOut']})
    }
    return (

                    <button className={styles.videoContainer} onMouseOver={onMouseOverButton} onMouseOut={onMouseOutButton} onClick={props.onClick}>
                        
                            <span className={styles.videoText}>{props.textButton} </span>
                        
                            
                            <svg width="29" height="28" viewBox="0 0 29 28" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <circle cx="14.5" cy="14" r="14" fill={colorArrowIcon['circle']}/>
                                <path d="M19.9805 14.0005L11.759 18.7472L11.759 9.25385L19.9805 14.0005Z" fill={colorArrowIcon['arrow']}/>
                            </svg>
                    </button>
                

          
  
    )
}