import React from 'react';
import { connect } from 'react-redux';
import styles from './PriceText.module.css'
export function PriceText({priceCrossOut, normalPrice}) {
    var priceCrossOutElement = null
    var normalPriceElement = null
    if (priceCrossOut) priceCrossOutElement = <span className={styles.textCrossOut}>{priceCrossOut}</span>
    if (normalPrice) normalPriceElement = <span className={styles.normalText}>{normalPrice}</span>
    return (
                <div className={styles.wrapper}> 
                    {priceCrossOutElement}
                    {normalPriceElement}
                </div>
    )
}