import React from 'react';
import { connect } from 'react-redux';
import styles from './LegalNotice.module.css'
import commonStyles from '../../App.module.css'
//import { PreReserveComponent } from './subcomponents/PreReserveComponent/PreReserveComponent.js';
import { BuyAndPrereserve } from './subcomponents/BuyAndPrereserveComponent/BuyAndPrereserveComponent';
import { MotoSharing } from './subcomponents/MotoSharing/MotoSharing';
import {BUY_AND_PRE_RESERVE_LEGAL_NOTICE_OPTION,
        MOTOSHARING_LEGAL_NOTICE_OPTION} from '../../reducer/constants';
import { LEGAL_NOTICE_STATE_PROPERTY } from '../../reducer/constants';
import { LEGAL_NOTICE_ACTION, ACTION_DISPATCHER } from '../../actions/constants';
import { StickyNavigation } from './subcomponents/StickyNavigation/StickyNavigation';
import { allActions } from '../../actions';
const LEGAL_NOTICE_TO_SHOW = {}
LEGAL_NOTICE_TO_SHOW[BUY_AND_PRE_RESERVE_LEGAL_NOTICE_OPTION] = <BuyAndPrereserve/>
LEGAL_NOTICE_TO_SHOW[MOTOSHARING_LEGAL_NOTICE_OPTION] = <MotoSharing/>


export function LegalNotice(props) {
   
    return (
        <div className={styles.wrapper}>
            <div className={styles.emptyDiv}>
           </div>
           <StickyNavigation/>
           <div className={`${styles.titleWrapper} ${commonStyles.wrapperMainWindow}`}>
                <div className={styles.title}>
                    Aviso Legal
                </div>
                <div className={styles.updateText}>
                    Fecha de actualización: 30/07/21
                </div>
           </div>
           <div className={`${styles.accordionWrapper} ${commonStyles.wrapperMainWindow}`}>
               <div className={styles.accordionsubWrapper}>
                    {LEGAL_NOTICE_TO_SHOW[props.legalNotice]}
               </div>

           </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
    
      legalNotice: state[LEGAL_NOTICE_STATE_PROPERTY],
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setLegalNotice: (payload) => dispatch(allActions[LEGAL_NOTICE_ACTION][ACTION_DISPATCHER](payload)),
    }
  }
 export default connect(mapStateToProps, mapDispatchToProps)(LegalNotice);