import React from 'react';
import styles from './SecondHeader.module.css';
import commonStyles from '../../Navbar.module.css';
import { connect } from 'react-redux';
import { ACTION_DISPATCHER, } from '../../../../actions/constants';  
import { ICON_HEADER_STATE_PROPERTY,
        SHOW_BURGUER_OPTIONS_STATE_PROPERTY,
        SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY,
        SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY, 
        SHOW_BACK_TO_MENU_STATE_PROPERTY,
        CLOSING_SECOND_HEADER_STATE_PROPERTY} from '../../../../reducer/constants';
import {OPEN_SECONDARY_WRAPPER_NAVIGATION_OPTION,
        CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION,
        SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION,
        SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION,
        SHOW_OFF_SECONDARY_NAVIGATION_OPTION,
        NOT_CLOSING_SECOND_HEADER_OPTION,
        YES_CLOSING_SECOND_HEADER_OPTION} from '../../../../reducer/constants';
import {ICON_HEADER_ACTION,
        SHOW_BACK_TO_MENU_ACTION,
        SECONDARY_NAV_WRAPPER_ACTION,
        SHOW_SECONDARY_NAV_ACTION,
        SHOW_BURGUER_ACTION,
        CLOSING_SECOND_HEADER_ACTION} from '../../../../actions/constants';
import { allActions } from '../../../../actions';
import BackToMenu from '../BackToMenu/BackToMenu';
import BurguerOptions from '../BurguerOptions/BurguerOptions';
import SecondaryNavigation from '../SecondaryNavigation/SecondaryNavigation';




const styleWrapperBurguerMenuOptionSecondaryNav = {}
styleWrapperBurguerMenuOptionSecondaryNav[OPEN_SECONDARY_WRAPPER_NAVIGATION_OPTION] = {}
styleWrapperBurguerMenuOptionSecondaryNav[CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION] = {display: 'none'}



const bottomWrapStyling = {}
bottomWrapStyling[SHOW_OFF_SECONDARY_NAVIGATION_OPTION] = {backgroundColor: 'transparent'}
bottomWrapStyling[SHOW_PRODUCTS_SECONDARY_NAVIGATION_OPTION] = {}
bottomWrapStyling[SHOW_SERVICES_SECONDARY_NAVIGATION_OPTION] = {}


const ClosingWrapperAnimation = {}
ClosingWrapperAnimation[NOT_CLOSING_SECOND_HEADER_OPTION] = ''
ClosingWrapperAnimation[YES_CLOSING_SECOND_HEADER_OPTION] = commonStyles.wrapperBurguerMenuOptionSecondaryNavClosing

export function SecondHeader(props) {
      
    return (
        
        <div >
            <BackToMenu/>
            <div style={styleWrapperBurguerMenuOptionSecondaryNav[props.secondaryNavWrapper]}>
                <div style={bottomWrapStyling[props.showSecondaryNav]} className={styles.bottomwrap}></div>
                <BurguerOptions/>
                <SecondaryNavigation/>
            </div>
        </div>
   
    )
  };

  const mapStateToProps = (state) => {
    return {
      iconHeader: state[ICON_HEADER_STATE_PROPERTY],
      showBackToMenu: state[SHOW_BACK_TO_MENU_STATE_PROPERTY],
      secondaryNavWrapper: state[SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY],
      showSecondaryNav: state[SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY],
      showBurguerOptions: state[SHOW_BURGUER_OPTIONS_STATE_PROPERTY],
      closeSecondHeader: state[CLOSING_SECOND_HEADER_STATE_PROPERTY]           
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setIconHeader: (payload) => dispatch(allActions[ICON_HEADER_ACTION][ACTION_DISPATCHER](payload)),
      setShowBackToMenu: (payload) => dispatch(allActions[SHOW_BACK_TO_MENU_ACTION][ACTION_DISPATCHER](payload)),
      setSecondaryNavWrapper: (payload) => dispatch(allActions[SECONDARY_NAV_WRAPPER_ACTION][ACTION_DISPATCHER](payload)),
      setShowBurguer: (payload) => dispatch(allActions[SHOW_BURGUER_ACTION][ACTION_DISPATCHER](payload)),
      setShowSecondaryNav: (payload) => dispatch(allActions[SHOW_SECONDARY_NAV_ACTION][ACTION_DISPATCHER](payload)),
      setCloseSecondHeader: (payload) => dispatch(allActions[CLOSING_SECOND_HEADER_ACTION][ACTION_DISPATCHER](payload)),
  
    }
  }
  export default connect(mapStateToProps, mapDispatchToProps)(SecondHeader);
  //export default Nav;