import React from 'react';
import { connect } from 'react-redux';
import styles from './PrivacyPolicy.module.css'
import commonStyles from '../../App.module.css'
import { Buy } from './subcomponents/Buy/Buy';
import { RentAndSuscription } from './subcomponents/RentAndSuscription/RentAndSuscription';
import { MotoSharing } from './subcomponents/MotoSharing/MotoSharing';
import {BUY_PRIVACY_POLICY_OPTION,
        RENT_AND_SUSCRIPTION_POLICY_OPTION,
        MOTOSHARING_POLICY_OPTION} from '../../reducer/constants';
import { PRIVACY_POLICY_STATE_PROPERTY } from '../../reducer/constants';
import { PRIVACY_POLICY_ACTION, ACTION_DISPATCHER } from '../../actions/constants';
import { StickyNavigation } from './subcomponents/StickyNavigation/StickyNavigation';
import { allActions } from '../../actions';
const PRIVACY_POLICY_TO_SHOW = {}
PRIVACY_POLICY_TO_SHOW[BUY_PRIVACY_POLICY_OPTION] = <Buy/>
PRIVACY_POLICY_TO_SHOW[RENT_AND_SUSCRIPTION_POLICY_OPTION] = <RentAndSuscription/>
PRIVACY_POLICY_TO_SHOW[MOTOSHARING_POLICY_OPTION] = <MotoSharing/>


export function PrivacyPolicy(props) {
   
    return (
        <div className={styles.wrapper}>
            <div className={styles.emptyDiv}>
           </div>
           <StickyNavigation/>
           <div className={`${styles.titleWrapper} ${commonStyles.wrapperMainWindow}`}>
                <div className={styles.title}>
                  Política de privacidad
                </div>
                <div className={styles.updateText}>
                    Fecha de actualización: 30/07/21
                </div>
           </div>
           <div className={`${styles.accordionWrapper} ${commonStyles.wrapperMainWindow}`}>
               <div className={styles.accordionsubWrapper}>
                    {PRIVACY_POLICY_TO_SHOW[props.privacyPolicy]}
               </div>

           </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
    
      privacyPolicy: state[PRIVACY_POLICY_STATE_PROPERTY],
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setPrivacyPolicy: (payload) => dispatch(allActions[PRIVACY_POLICY_ACTION][ACTION_DISPATCHER](payload)),
    }
  }
 export default connect(mapStateToProps, mapDispatchToProps)(PrivacyPolicy);