import React, {useEffect, useId} from 'react';
import { connect } from 'react-redux';
import styles from './VideoHome.module.css'


export function VideoHome(props) {
    const videoId = useId()
    useEffect(()=>{
        const videoElement = document.getElementById(videoId)
        videoElement.autoplay = true
        videoElement.muted = true
        videoElement.play()
    },[])
    return (
        <div >
           
            <video controls style={{width:'100vw', height: '100vh', objectFit: 'cover'}} id={videoId}>
                <source width="320" height="240" src={"https://seatmo-video.s3.eu-west-1.amazonaws.com/MO_VERANO_LONG_EDIT40.mp4"} type="video/mp4"></source>
            </video>
           
        </div>
    )
}