import React from 'react';
import { connect } from 'react-redux';
import styles from './MenuProductCardComponent.module.css';
import { PrimaryButton } from '../buttons/primaryButton/primaryButton.js';
export function MenuProductCardComponent(props) {
    const [maxWidthImage, setMaxWidthImage] = React.useState('100%');
    function getImgSize(imgSrc) {
        var newImg = new Image();
        newImg.onload = function() 
        {
            var width = 139*(newImg.width/newImg.height);
            setMaxWidthImage(width+'px')
        }
        newImg.src = imgSrc; // this must be done AFTER setting onload
    }
    getImgSize(props.imgSource)
    return (

               
                <div className={styles.card}> 
                    <div className={styles.cardTitle}>
                        {props.title}
                    </div>
                    <div className={styles.cardPrice}>
                        {props.price}
                    </div>
                    <div className={styles.imageContainer}>
                        <img style={{maxWidth:maxWidthImage}} src={props.imgSource} alt="Scooter" width={'100%'} height={'100%'}/>
                    </div>
                    <div className={styles.horizontanLine}>
                        
                    </div>
                    <PrimaryButton textButton='Ver'/>
                </div>

          
  
    )
}