import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import styles from './Cart.module.css';
import { connect } from 'react-redux';
import {PATH_ABOUTUS,
        PATH_ACCESORIES,
        PATH_CART,
        PATH_HELPECENTER,
        PATH_PRODUCTS,
        PATH_SERVICES} from '../../../constants.js'
import { useEffect } from 'react';
import { ACTION_DISPATCHER, } from '../../../../actions/constants';  
import { ICON_HEADER_STATE_PROPERTY,
        SHOW_BURGUER_OPTIONS_STATE_PROPERTY,
        SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY,
        SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY, 
        SHOW_BACK_TO_MENU_STATE_PROPERTY} from '../../../../reducer/constants';
import {ICON_HEADER_ACTION,
        SHOW_BACK_TO_MENU_ACTION,
        SECONDARY_NAV_WRAPPER_ACTION,
        SHOW_SECONDARY_NAV_ACTION,
        SHOW_BURGUER_ACTION} from '../../../../actions/constants';
import { allActions } from '../../../../actions';





const initialStylingText = {}
initialStylingText[PATH_ABOUTUS] = styles.textDefaultNav
initialStylingText[PATH_PRODUCTS] = styles.textDefaultNav
initialStylingText[PATH_SERVICES] = styles.textDefaultNav
initialStylingText[PATH_ACCESORIES] = styles.textDefaultNav
initialStylingText[PATH_HELPECENTER] = styles.textDefaultNav
initialStylingText[PATH_CART] = styles.textDefaultNav








export function Cart(props) {
    
    const [styleText, setStyleText] = React.useState({...initialStylingText});

    
    const location = useLocation();
    useEffect(() => {
        
        const objStyle = {...initialStylingText}
        objStyle[location.pathname] = styles.textSelectedNav
        setStyleText({...objStyle})        
      }, [location]);

  
  
    
    return (            
                <Link to={PATH_CART} className={styles.linkCart} >
                    <div  className={styles.cartContainer}>
                        <div className={`${styleText[PATH_CART]} ${styles.cartText}`}>
                            <span>Carrito</span>
                        </div> 
                        <div className={styles.svgCartContainer}>
                            <svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <circle cx="14" cy="14" r="14" fill="#FE3B1F">
                                </circle>
                        
                            </svg>
                            <div className={styles.nrCartText}>
                                88
                            </div>
                        </div>              
                    </div>
                </Link>
                
           
    )
  };

  const mapStateToProps = (state) => {
    return {
      iconHeader: state[ICON_HEADER_STATE_PROPERTY],
      showBackToMenu: state[SHOW_BACK_TO_MENU_STATE_PROPERTY],
      secondaryNavWrapper: state[SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY],
      showSecondaryNav: state[SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY],
      showBurguerOptions: state[SHOW_BURGUER_OPTIONS_STATE_PROPERTY]       
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setIconHeader: (payload) => dispatch(allActions[ICON_HEADER_ACTION][ACTION_DISPATCHER](payload)),
      setShowBackToMenu: (payload) => dispatch(allActions[SHOW_BACK_TO_MENU_ACTION][ACTION_DISPATCHER](payload)),
      setSecondaryNavWrapper: (payload) => dispatch(allActions[SECONDARY_NAV_WRAPPER_ACTION][ACTION_DISPATCHER](payload)),
      setShowBurguer: (payload) => dispatch(allActions[SHOW_BURGUER_ACTION][ACTION_DISPATCHER](payload)),
      setShowSecondaryNav: (payload) => dispatch(allActions[SHOW_SECONDARY_NAV_ACTION][ACTION_DISPATCHER](payload)),
  
    }
  }
  export default connect(mapStateToProps, mapDispatchToProps)(Cart);
  //export default Nav;