import React from 'react';
import { useEffect } from 'react';
import styles from './BurguerIcon.module.css';
import { connect } from 'react-redux';
import { ACTION_DISPATCHER, } from '../../../../actions/constants';  
import { ICON_HEADER_STATE_PROPERTY,
        SHOW_BURGUER_OPTIONS_STATE_PROPERTY,
        SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY,
        SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY, 
        SHOW_BACK_TO_MENU_STATE_PROPERTY,
        CLOSING_SECOND_HEADER_STATE_PROPERTY} from '../../../../reducer/constants';
import {CLOSED_ICON_HEADER_OPTION,
        OPENED_ICON_HEADER_OPTION,
        OPEN_SECONDARY_WRAPPER_NAVIGATION_OPTION,
        CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION,
        SHOW_OFF_BACK_TO_MENU_OPTION,
        SHOW_OFF_SECONDARY_NAVIGATION_OPTION,
        SHOW_ON_BURGUER_OPTIONS,
        SHOW_OFF_BURGUER_OPTIONS,
        YES_CLOSING_SECOND_HEADER_OPTION,
        NOT_CLOSING_SECOND_HEADER_OPTION} from '../../../../reducer/constants';
import {ICON_HEADER_ACTION,
        SHOW_BACK_TO_MENU_ACTION,
        SECONDARY_NAV_WRAPPER_ACTION,
        SHOW_SECONDARY_NAV_ACTION,
        SHOW_BURGUER_ACTION,
        CLOSING_SECOND_HEADER_ACTION} from '../../../../actions/constants';
import { allActions } from '../../../../actions';


const iconsBurguerOrClose = {}
iconsBurguerOrClose[CLOSED_ICON_HEADER_OPTION] =    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                        <path d="M0.676114 5.02815C0.497767 5.02551 0.325672 5.09373 0.198661 5.21718C0.0716512 5.34085 0 5.50942 0 5.68524C0 5.86129 0.0716512 6.02986 0.198661 6.15331C0.325672 6.27698 0.497767 6.34498 0.676114 6.34256H23.3239C23.5022 6.34498 23.6743 6.27698 23.8013 6.15331C23.9284 6.02986 24 5.86129 24 5.68524C24 5.50942 23.9284 5.34085 23.8013 5.21718C23.6743 5.09373 23.5022 5.02551 23.3239 5.02815H0.676114Z" fill="white"/>
                                                        <path d="M0.676114 11.3426C0.497767 11.3402 0.325672 11.4084 0.198661 11.5318C0.0716512 11.6553 0 11.8239 0 11.9999C0 12.176 0.0716512 12.3445 0.198661 12.468C0.325672 12.5914 0.497767 12.6596 0.676114 12.6572H23.3239C23.5022 12.6596 23.6743 12.5914 23.8013 12.468C23.9284 12.3445 24 12.176 24 11.9999C24 11.8239 23.9284 11.6553 23.8013 11.5318C23.6743 11.4084 23.5022 11.3402 23.3239 11.3426H0.676114Z" fill="white"/>
                                                        <path d="M0.676114 17.6575C0.497767 17.6551 0.325672 17.7231 0.198661 17.8468C0.0714283 17.9702 0 18.1388 0 18.3148C0 18.4907 0.0714283 18.6592 0.198661 18.7829C0.325672 18.9064 0.497767 18.9746 0.676114 18.9719H23.3239C23.5025 18.9746 23.6743 18.9064 23.8013 18.7829C23.9286 18.6592 24 18.4907 24 18.3148C24 18.1388 23.9286 17.9702 23.8013 17.8468C23.6743 17.7231 23.5025 17.6551 23.3239 17.6575H0.676114Z" fill="white"/>
                                                    </svg>
iconsBurguerOrClose[OPENED_ICON_HEADER_OPTION] =    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                        <path fillRule="evenodd" clipRule="evenodd" d="M3.98591 3.32403C4.16301 3.32655 4.33294 3.4 4.45719 3.52798L11.9996 11.0704L19.542 3.52796C19.6665 3.39998 19.8362 3.32653 20.0134 3.32417C20.1907 3.32149 20.3604 3.39018 20.4849 3.51466C20.6092 3.63899 20.6779 3.80869 20.6754 3.98611C20.6728 4.16321 20.5996 4.33298 20.4714 4.45738L12.929 11.9998L20.4716 19.5424C20.5996 19.6666 20.673 19.8365 20.6755 20.0136C20.6779 20.1909 20.6094 20.3608 20.485 20.4851C20.3605 20.6096 20.1907 20.6781 20.0136 20.6756C19.8363 20.6732 19.6666 20.5996 19.5422 20.4718L11.9996 12.9292L4.45702 20.4718C4.33278 20.5998 4.16285 20.6732 3.98575 20.6757C3.80833 20.6782 3.63863 20.6096 3.5143 20.4852C3.38982 20.3607 3.32113 20.191 3.32381 20.0138C3.32617 19.8365 3.39977 19.6667 3.52759 19.5424L11.0701 11.9998L3.52776 4.45741C3.39994 4.333 3.32633 4.16323 3.32397 3.98597C3.32145 3.80887 3.38998 3.63901 3.51447 3.51453C3.63879 3.3902 3.80865 3.32167 3.98591 3.32403Z" fill="white"/>
               
                                                    </svg>
const HIDE_BURGUER_TEMPORARLY = 'hideBurguerIcon'
const animationBurguer = {}
animationBurguer[OPENED_ICON_HEADER_OPTION] = { animation: ''}
animationBurguer[CLOSED_ICON_HEADER_OPTION] = { animation: ''}
animationBurguer[HIDE_BURGUER_TEMPORARLY]  = {animation: 'none', visible: 'hidden'}

export function BurguerIcon(props) {
    const [animationBurguerIcon, setAnimationBurguerIcon] = React.useState({ display: 'none'});

  useEffect(() => {
    
    setTimeout(()=> {
        setAnimationBurguerIcon({...animationBurguer[props.iconHeader]})
    }, 10)
           
  }, [props.iconHeader]);

    function showIcon (event)
    {

        event.preventDefault()  
        if (props.iconHeader === CLOSED_ICON_HEADER_OPTION)
        {
            setAnimationBurguerIcon({...animationBurguer[HIDE_BURGUER_TEMPORARLY]})
            props.setCloseSecondHeader(NOT_CLOSING_SECOND_HEADER_OPTION)
            setTimeout(()=>{
              props.setIconHeader(OPENED_ICON_HEADER_OPTION)
              props.setCloseSecondHeader(NOT_CLOSING_SECOND_HEADER_OPTION)
              props.setSecondaryNavWrapper(OPEN_SECONDARY_WRAPPER_NAVIGATION_OPTION)
              props.setShowBurguer(SHOW_ON_BURGUER_OPTIONS)
            },100)
            
        }
        else if (props.iconHeader === OPENED_ICON_HEADER_OPTION)
        {
             setAnimationBurguerIcon({...animationBurguer[HIDE_BURGUER_TEMPORARLY]})
             setTimeout(()=>{
              props.setCloseSecondHeader(YES_CLOSING_SECOND_HEADER_OPTION)

              
             },15)
            
            setTimeout(() => {
              props.setIconHeader(CLOSED_ICON_HEADER_OPTION)
              props.setCloseSecondHeader(YES_CLOSING_SECOND_HEADER_OPTION)
              props.setShowBackToMenu(SHOW_OFF_BACK_TO_MENU_OPTION)
              props.setSecondaryNavWrapper(CLOSED_SECONDARY_WRAPPER_NAVIGATION_OPTION)
              props.setShowBurguer(SHOW_OFF_BURGUER_OPTIONS)
              props.setShowSecondaryNav(SHOW_OFF_SECONDARY_NAVIGATION_OPTION)
            }, 330);
            
            
        }
    }
    
  
    return (
                <div onClick={showIcon} className={styles.burguerIcon} style={animationBurguerIcon}>
                    {iconsBurguerOrClose[props.iconHeader]}
                </div>
            )
  };

  const mapStateToProps = (state) => {
    return {
      iconHeader: state[ICON_HEADER_STATE_PROPERTY],
      showBackToMenu: state[SHOW_BACK_TO_MENU_STATE_PROPERTY],
      secondaryNavWrapper: state[SECONDARY_NAVIGATION_WRAPPER_STATE_PROPERTY],
      showSecondaryNav: state[SHOW_SECONDARY_NAVIIGATION_STATE_PROPERTY],
      showBurguerOptions: state[SHOW_BURGUER_OPTIONS_STATE_PROPERTY],
      closeSecondHeader : state[CLOSING_SECOND_HEADER_STATE_PROPERTY]         
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setIconHeader: (payload) => dispatch(allActions[ICON_HEADER_ACTION][ACTION_DISPATCHER](payload)),
      setShowBackToMenu: (payload) => dispatch(allActions[SHOW_BACK_TO_MENU_ACTION][ACTION_DISPATCHER](payload)),
      setSecondaryNavWrapper: (payload) => dispatch(allActions[SECONDARY_NAV_WRAPPER_ACTION][ACTION_DISPATCHER](payload)),
      setShowBurguer: (payload) => dispatch(allActions[SHOW_BURGUER_ACTION][ACTION_DISPATCHER](payload)),
      setShowSecondaryNav: (payload) => dispatch(allActions[SHOW_SECONDARY_NAV_ACTION][ACTION_DISPATCHER](payload)),
      setCloseSecondHeader: (payload) => dispatch(allActions[CLOSING_SECOND_HEADER_ACTION][ACTION_DISPATCHER](payload)),
  
    }
  }
  export default connect(mapStateToProps, mapDispatchToProps)(BurguerIcon);
  //export default Nav;